.main{
    width: 100%;
    height: 100vh;
    /* background-color: aqua; */
}
nav{
    height: 80px;
    width: 100%;
    background: black;
    display: flex;
}
.logo{
    height: 80px;
    width: 150px;
    /* background: yellow; */
    margin-left: 150px;
}
.logo > h2{
   font-size: 28px;
   font-weight: 600;
   color: white;
   padding: 0 35px;
}
ul{
    height: inherit;
    margin-top: 0px;
    width:60%;
    /* background: green; */
    color: white;
    list-style-type: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
ul > li{
    cursor: pointer;
}
#btn{
    height: 50px;
    width: 150px;
    font-size: 17px;
    color: white;
    margin: 15px 40px;
    background: none;
    cursor: pointer;
    /* background: red; */
}
#btn > a{
    text-decoration: none;
    color: white;
}
.container{
    height: 89.3vh;
    width: 100%;
    background-image: url('https://w0.peakpx.com/wallpaper/410/172/HD-wallpaper-at-quartz-bridge-2021-metaverse-vr-poster.jpg');
    background-position: center;
    background-size: cover;
    display: flex;
}
.main_box_1{
    height: 89.3vh;
    width: 50%;
    /* background: red; */
    display: flex;
}
.box_1{
    width: 30px;
    height: inherit;
    /* background: green; */
    display: flex;
    align-items: flex-end;
}
.sub_box{
    height: 50px;
    width: 10px;
    background-color: rgb(254, 6, 48);
}
.box_2{
    width: 280px;
    height: inherit;
    /* background: blue; */
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.sub_box_1{
    width: 280px;
    height: 170px;
    /* background: rebeccapurple;  */
    margin-top: 30px;
}
.sub_box_2{
    width: 220px;
    height: 140px;
    /* background: red;   */
