    font-weight: 100;
}
.main_box_2{
    height: 89.3vh;
    width: 50%;
    /* background: green;  */
    display: flex;
}
.sub_box_3{
    height:inherit;
    width: 90%;
    /* background: black;   */
}
.sub_box_3 > h1{
   font-size: 40px;
   font-weight: 700;
   color: white;
   padding-top: 50px;
   padding-left: 250px;
}
.sub_box_3 > h3{
    font-size: 15px;
    font-weight: 200;
    color: lightgray;
    padding-top: 50px;
    padding-left: 250px;
    line-height: 25px;
 }
.main_btn{
    margin: 40px 250px;
    padding: 11px 15px;
    font-weight: 200;
    cursor: pointer;
    background-color: #e60073;
    outline: none;
    border: none;
    color: white;
    -webkit-animation: glow 1s ease-in-out infinite alternate;
    -moz-animation: glow 1s ease-in-out infinite alternate;
    animation: glow 1s ease-in-out infinite alternate;
}
@-webkit-keyframes glow {
    from {
      text-shadow: 0 0 1px #fff, 0 0 1px #fff, 0 0 1px #fff, 0 0 1px #fff;
    }
    to {
      text-shadow:0 0 1px #fff, 0 0 1px #fff, 0 0 1px #fff, 0 0 1px #fff;
    }
  }
.sub_box_4{
    height:inherit;
    width: 10%;
