    flex-direction: column;
    justify-content: flex-end;
}
.telegram_box{
    height: 40px;
    width: 40px;
    background: linear-gradient(to right,#e60073,#039be5);
    border-radius: 4px;
}
.icon{
    font-size: 18px;
    color: white;
    margin: 10px 10px;
}
.message_box{
    height: 55px;
    width: 55px;
    border-radius: 50%;
    background-color: #039be5;
   margin: 8px 8px;
}
.icons{
    font-size: 25px;
    color: white;
    margin: 14px 15px;
}
