.signup{
    height: 100vh;
    width: 100%;
    /* background-color: aqua; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.login_form{
    height: 350px;
    width: 350px;
    background-color: white;
    border-radius: 15px;
    border: 1px solid lightgray;
}
.login_form > h2{
    padding: 0 90px;
}
.user_name{
    height: 25px;
    width: 80px;
}
.name{
    margin: 30px 10px;
}
.input{
    height: 30px;
    width: 230px;
    text-indent: 10px;
    outline: none;
    margin:8px 0px;
}
.create_btn{
    padding: 10px 91px;
    margin-left: 25px;
    margin-top: 20px;  
    font-size: 15px;
    cursor: pointer;
}



